

































import Vue from 'vue';

export default Vue.extend({
  name: 'TopNavi',
  data() {
    return {
      routes: [
        {route: '/', name: 'Mirrors'},
        {route: '/download', name: 'Download'},
        {route: '/help', name: 'Help'},
        {route: '/about', name: 'About'},
      ]
    };
  },
  computed: {
    active(): string {
      return '/' + this.$route.path.split('/')[1];
    },
    isMobile(): boolean {
      return this.$store.state.isMobile;
    }
  },
});
